import React from 'react';
import Layout from "../components/layout"

const Philosophie = ({ data }) => {
  return (
    <Layout>
      <div className="philosophie page">
        <h1>{data.allStrapiPagePhilosophie.nodes[0].titre}</h1>

        <div className="page__content">
          <p className="page__content__main">{data.allStrapiPagePhilosophie.nodes[0].description}</p>
          <p className="page__content__margin">{data.allStrapiPagePhilosophie.nodes[0].description}</p>
        </div>

      </div>
    </Layout>
  );
};

export default Philosophie;

export const pageQuery = graphql`
  query PhilosophiePageQuery {
    allStrapiPagePhilosophie {
        nodes {
          information
          description
          titre
        }
    }
  }
`